:root {
  --acc-color: #c7a17a;
  --main-bcg: #f2f2f2;
  --section-bcg: #415167;
  --alt-section-bcg: #edf0f5;
  --main-f-color: #151d28;
  --main-title-f-color: #415167;
  --other-title-f-color: #232c38;
}

@media screen and (min-width: 320px) {
  html, body {
    overflow-x: hidden;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

body {
  background-color: var(--modal-bcg);
  font-family: Nunito;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

link {
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  line-height: 0;
}

.visually-hiden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 130px;
